// sizes
.site-wrapper {
  width: 100%;
  max-width: $size__max-width;
}

.section-pt-2 {
  padding-top: $section__padding-80;
}

.section-pb-2 {
  padding-bottom: $section__padding-80;
}

.section-pt-3 {
  padding-top: $section__padding-60;
}

.section-pb-3 {
  padding-bottom: $section__padding-60;
}

.section-pt-4 {
  padding-top: $section__padding-25;
}

.section-pb-4 {
  padding-bottom: $section__padding-25;
}

// colors
.section-bg-interactive {
  background-color: $color__background-interactive;
}

.section-bg-main {
  background-color: $color__background-main;
}

.section-bg-dark {
  background-color: $color__background-dark;
}

// positioning
.block-absolute {
  @include absolute;
}

.block-overlay {
  background: rgba(0, 0, 0, .3);
  @include absolute;
  @include transition;
}

.block-centering {
  @include centering;
}